// pages/test.js
import React, { useState, useEffect } from 'react';
import { graphql } from 'gatsby';


import Layout from '../components/main-layout-ja';
import Seo from '../components/seo';

//Image
import ListBg from '../images/bg/list.svg';
import ListBgSp from '../images/bg/list-sp.svg';

const NewsPage = ({ data }) => {
  const currentDate = new Date().toISOString();
  data = data.allMicrocmsPosts.edges.filter(
    ({ node }) => node.public_date <= currentDate
  );
  data.forEach(function(article){
    const date = new Date(article.node.public_date);
    const formattedDate = date.toLocaleDateString('ja-JP', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
    });
    const desiredFormat = formattedDate.replace(/(\d+)\/(\d+)\/(\d+)/, '$1/$2/$3');
    article.node.public_date_formatted = desiredFormat;
  });

  const allNews = data;
  // State for the list
  const [list, setList] = useState([...allNews.slice(0, 9)]);

  // State to trigger oad more
  const [loadMore, setLoadMore] = useState(false);

  // State of whether there is more to load
  const [hasMore, setHasMore] = useState(allNews.length > 10);
  
  // Load more button click
  const handleLoadMore = () => {
    setLoadMore(true);
  };

  //Check if there is more
  useEffect(() => {
    const isMore = list.length < allNews.length;
    setHasMore(isMore);
  }, [list]); //eslint-disable-line

  // Handle loading more articles
  useEffect(() => {
    if (loadMore && hasMore) {
      const currentLength = list.length;
      const isMore = currentLength < allNews.length;
      const nextResults = isMore
        ? allNews.slice(currentLength, currentLength + 10)
        : [];
      setList([...list, ...nextResults]);
      setLoadMore(false);
    }
  }, [loadMore, hasMore]); //eslint-disable-line
  return <Layout>
    <Seo title='お知らせ' />

    <section className="news">
      <picture>
          <source media="(min-width: 768px)" srcSet={ListBg} />
          <img className="list__bg" src={ListBgSp} alt="お知らせ" />
      </picture>
      <div className="default-max-width">
        <h2 className="heading heading--no-line">
          お知らせ
        </h2>
        <div className="news__list-container">
          <ul className="news__list">
            {list.map(edge => {
                const article = edge.node

                return (
                <React.Fragment key={article.id}>
                  <li className="news__item">
                    <div className="news__date-container">
                      <span className="tag tag--blue">{article.category[0]}</span>
                    </div>
                    <a href={ "/news/" + article.id } className="news__title">{ article.title_ja }</a>
                  </li>
                </React.Fragment>
                )
            })}
          </ul>
        </div>
        {
          hasMore
            ? <button className="button" onClick={handleLoadMore}>もっと見る</button>
            : null
        }
      </div>
    </section>
  </Layout>
}

export const query = graphql`
  {
    allMicrocmsPosts(
      sort: { fields: [public_date, createdAt], order: DESC }
    ) {
      edges {
        node {
          id
          title_ja
          public_date
          category
        }
      }
    }
  }
`

export default NewsPage